import { Messengers } from "../helpers/Messengers";
import { Store } from "../helpers/seven-store";
import { ErrorTypes, MLEError, setTokenInDeviceStorage } from "../../provider-utils";
import { 
    getLivePlaybackSourceMockHelper, 
    shouldHandleLocationOrWrongChannelError, 
} from "./featureFlag-helper"
import { generateUUID } from "../helpers/utils";

const LIVE_STREAM_TYPE = "application/vnd.apple.mpegurl";

const proxyUrl = "https://au-proxy.apac.ninja/seven-proxy";

const isUpdatedAuth2ApiEnabled = Store.isUpdatedAuth2ApiEnabled
const isAflGeoEnabled = Store.isAflGeoEnabled

export const makeSevenAPIRequest = async (url, fetchInit = undefined) => {
    const newFetchInit = {
        ...fetchInit,
        headers: {
            ...fetchInit?.headers,
        },
    };

    if (!Store.useProdConfig) {
        newFetchInit.headers["Target-URL"] = encodeURI(url);
        return await fetch(proxyUrl, newFetchInit);
    }

    return await fetch(url, newFetchInit);
};

export const makeSevenPlaybackAPIRequest = async (url, fetchInit = {}) => {
    const newFetchInit = {
        ...fetchInit,
        headers: {
            ...fetchInit.headers,
        },
    };

    try {
        // Use proxy URL if not in production config
        const response = await (Store.useProdConfig 
            ? fetch(url, newFetchInit) 
            : fetch(proxyUrl, { ...newFetchInit, headers: { ...newFetchInit.headers, "Target-URL": encodeURI(url) } })
        );

        if (!response.ok) {
            const error = await getErrorResponse(response);
            console.warn('API request not ok:', JSON.stringify(error));
            return { error: response.status, errorJson: error };
        }

        return await response.json();
    } catch (e) {
        console.error('Fetch error:', e);
        return { error: 'Fetch error', details: e.message };
    }
};

const getErrorResponse = async (response) => {
    try {
        return await response.json();
    } catch (e) {
        console.warn('Failed to parse error response as JSON:', e);
        return { error: 'Unknown error' }; // Fallback in case parsing fails
    }
};


const buildVideoUrl = (skeletonUrl, options) => {
    const { isDrmEnabled, isDrmMockingEnabled } = options || {};
    // @TODO change advertId to use PSID id once Samsung confirmed
    Store.setOzTamId(generateUUID());

    const deviceId = Store.deviceId;
    const postcode = Store.RegionData.postcode;
    const deliveryId = "ssaibeacon";
    let videoUrl = skeletonUrl
        .replace("{ppId}", "")
        .replace("{deliveryId}", deliveryId)
        .replace("[advertid]", deviceId)
        .replace("[postcode]", postcode)
        .replace("[deviceId]", deviceId);

    const ozidParam = `${skeletonUrl.indexOf("?") >= 0 ? "&" : "?"}ozid=`;
    videoUrl += ozidParam + Store.getOzTamId();

    if (isDrmMockingEnabled) {
        videoUrl = getLivePlaybackSourceMockHelper(videoUrl)
    }

    // Append DRM params to video service request.
    if (isDrmEnabled) {
        videoUrl += `&cp.containerFormat=ts|cmaf&cp.drmSystems=widevine&cp.encryptionType=cenc&cp.drmAuth=true`;
    }

    return videoUrl;
};

export const getSevenPlaybackData = async (channelCode) => {
    const apiBaseUrl = Store.AppConfig.endpoints.BASE_URL;
    const sevenChannelUrl = `${apiBaseUrl}player/live/${channelCode}?platform-id=${Store.Api.platformId}&market-id=${Store.RegionData.marketKey}&api-version=${Store.Api.apiVersion}&signedUp=True&platform-version=${Store.Api.platformId}`;

    let skeletonUrl;
    try {
        const channelRequest = await makeSevenAPIRequest(sevenChannelUrl);

        if (!channelRequest.ok) {
            throw channelRequest;
        }

        const channelResponse = await channelRequest.json();
        const videoData = channelResponse.videoPlayer;

        if (!videoData)
            throw new Error("No matching video in channel response");

        if (videoData.videoUrl) {
            skeletonUrl = videoData.videoUrl;
            Messengers.streamReady$.send(videoData);
        } else {
            throw new Error("Channel videoUrl not found");
        }
    } catch (err) {
        if (err.status === 404) {
            throw new MLEError(
                `Could not find channel with code ${channelCode} on 7Plus API`,
                ErrorTypes.CHANNEL_MAP,
                err.status
            );
        }
        throw new MLEError(err.message, ErrorTypes.PROVIDER, err.status);
    }

    const { LIVE_DRM_ENABLED, LIVE_DRM_MOCKING_ENABLED } = Store.AppConfig.config;
    const isDrmEnabled = LIVE_DRM_ENABLED ?? false;
    const isDrmMockingEnabled = LIVE_DRM_MOCKING_ENABLED ?? false;
    const videoUrl = buildVideoUrl(skeletonUrl, { isDrmEnabled, isDrmMockingEnabled });

    try {
        const playbackResponse = await makeSevenPlaybackAPIRequest(videoUrl, {
            headers: { Authorization: "Bearer " + Store.Auth.access_token },
        });

        if (playbackResponse?.error) {
            throw playbackResponse;
        }

        let matchingSource;
        if (isDrmEnabled && playbackResponse?.media?.stream_type_drm) {
            matchingSource = playbackResponse.media.sources.find(
                (channel) => channel.encryption_type === "cenc"
            );
        } else {
            matchingSource = playbackResponse.media.sources.find(
                (channel) => channel.type === LIVE_STREAM_TYPE
            );
        }
        if (!matchingSource) {
            throw new Error("Could not find matching live object");
        }

        Messengers.playbackData$.send({ matchingSource, playbackResponse });
        const licenseAuthorizationToken =
            matchingSource["key_systems"]?.authorization?.token;
        const licenseRequestHeaders = [
            {
                key: "Authorization",
                value: `Bearer ${licenseAuthorizationToken}`,
                isLicenseRequest: true,
            },
            {
                key: "Content-Type",
                value: "application/octet-stream",
                isLicenseRequest: true,
            },
        ];
        return {
            sourceUrl: matchingSource.src,
            drmLicenseUrl:
                matchingSource["key_systems"]?.[Store.preferredDrmSystem]
                    ?.license_url,
            ...(licenseAuthorizationToken && {
                requestHeaders: licenseRequestHeaders,
            }),
        };
    } catch (err) {
        // check for AFL location verification error
        if (shouldHandleLocationOrWrongChannelError(err)) {
            // need to set geoRestricted Flag to true and naviate to sign in page
            setTokenInDeviceStorage(
                "au.com.seven",
                btoa(
                    JSON.stringify({
                        refresh_token: Store.Auth.refresh_token,
                        access_token: Store.Auth.access_token,
                        geoRestricted: true
                    })
                )
            );
            throw new MLEError(
                "Could not play the 7Plus video geo hard block",
                ErrorTypes.AUTH,
                err.status
            );
        } else {
            console.error('errs:' + err)
            throw new MLEError(err.message, ErrorTypes.PROVIDER, err.status);
        }
    }
};

export const getAndStoreRegion = async () => {
    let regionUrl;
    const isLocationApiEnabled = Store.isLocationApiEnabled

    if (isLocationApiEnabled) {
        regionUrl = Store.AppConfig.endpoints.LOCATION_URL;
    } else {
        regionUrl = Store.AppConfig.endpoints.GEO_LOCATION_URL;
    }

    regionUrl = regionUrl.replace('{deviceId}', Store.deviceId)
    const regionRequest = await makeSevenAPIRequest(regionUrl);
    let regionResponse;

    if (!regionRequest.ok) {
        regionResponse = isLocationApiEnabled ? {
            market_id: Number(Store?.getAppConfig()?.config.DEFAULT_REGION) || -1,
            postcode: '',
            suburb: '',
            user_market: '',
            verified: false
        } : {
            _id: Number(Store.AppConfig.config.DEFAULT_REGION),
            postcode: '',
            place_name: '',
            ip_address: ''
        };
    } else {
        regionResponse = await regionRequest.json();
    }

    if (isLocationApiEnabled) {
        Store.RegionData = {
            regionKey: regionResponse.suburb,
            marketKey: regionResponse.market_id.toString(),
            postcode: regionResponse.postcode,
            userMarket: regionResponse.user_market,
            isLocationVerified: regionResponse.verified
        };
    } else {
        Store.RegionData = {
            regionKey: regionResponse.place_name,
            marketKey: regionResponse._id.toString(),
            postcode: regionResponse.postcode,
            ipAddress: regionResponse.ip_address,
        };
    }
   
};

export const getAndStoreConfig = async () => {
    let jsonVersion = isAflGeoEnabled && isUpdatedAuth2ApiEnabled ? '1.0' : '0.3'
    const getConfigUrl = `${Store.Api.configBase}/${Store.Api.platformId}/${jsonVersion}.json?version=${Store.Api.platformVersion}`;
    const configRequest = await makeSevenAPIRequest(getConfigUrl);

    if (!configRequest.ok) {
        throw new MLEError(
            "App config request failed",
            ErrorTypes.PROVIDER,
            configRequest.status
        );
    }
    const configResponse = await configRequest.json();
    Store.AppConfig = configResponse;
};
