import { Store } from "../helpers/seven-store";

export const StorageTypes = {
  IsContentRestricted: 'djp.contentrestricted',
  LiveDrmExpiryDuration: 'djp.duration',
  MockMarketId: 'djp.marketid',
  MockRestrictionPrompt: 'djp.contentstarttime',
};

export const getLocalStorageItem = (key) => localStorage.getItem(key);

export const setLocalStorageItem = (key, value) => {
  localStorage.setItem(key, value);
};

export const removeLocalStorageItem = (key) => {
  localStorage.removeItem(key);
};

export const getLivePlaybackSourceMockHelper = (videoUrl) => {
  const isLiveDrmMockingEnabled =
    Store.AppConfig && Store.AppConfig.config
      ? Store.AppConfig.config.LIVE_DRM_ENABLED
      : false;

  if (!isLiveDrmMockingEnabled) {
    return videoUrl;
  }

  let modifiedVideoUrl = videoUrl;
  const { IsContentRestricted, LiveDrmExpiryDuration, MockMarketId, MockRestrictionPrompt } = StorageTypes;
  const geoMockingLocalStorageKeys = [
    IsContentRestricted,
    LiveDrmExpiryDuration,
    MockMarketId,
    MockRestrictionPrompt,
  ];

  geoMockingLocalStorageKeys.forEach((localStorageKey) => {
    const paramValue = getLocalStorageItem(localStorageKey);

    if (paramValue) {
      modifiedVideoUrl += `&${localStorageKey}=${paramValue}`;
    }
  });

  return modifiedVideoUrl;
};

export const shouldHandleLocationOrWrongChannelError = (error) => {
  const isFeatureEnabled =
    Store.isAflGeoEnabled && Store.isPlaybackLocationVerificationEnabled;

  const errorMessage = ['location_verification_required', 'channel_not_in_market'].includes(
    error?.errorJson?.error
  )
  return (
    isFeatureEnabled &&
    errorMessage &&
    error?.error === 403
  );
};

