import "../lib/mux-analytics";

import { Store } from "../helpers/seven-store";
import { Messengers } from "../helpers/Messengers";
import { PlayerStates } from "../helpers/utils";

// custom javascript implementation
// https://docs-legacy.mux.com/guides/data/custom-javascript-integration

class MuxClient {
    envKey = null;
    playbackData = null;
    playbackSource = null;
    playerState = null;
    muxPlayerId = "mux-mle";
    currentTimeMs = 0;
    hasPlayed = false;
    currentAd = null;

    listeners = [];

    init = async () => {
        if (!window.mux) {
            console.warn("[acc] No mux analytics on window");
            return;
        }
        const muxEnabled = Store.AppConfig?.config?.mux?.MUX_ENABLED;
        const muxKey = Store.AppConfig?.config?.mux?.MUX_PROPERTY_KEY;
        if (muxEnabled && muxKey) {
            this.envKey = muxKey;
            this.removeEventListeners(); // in case monitoringTeardown isn't called
            this.addEventListeners();
        } else {
            console.warn(
                "[acc] No mux key or enabled flag in config, doing nothing"
            );
        }
    };

    addEventListeners = () => {
        this.listeners = [
            ...this.listeners,
            Messengers.playbackData$.addReceiver(
                this.onPlaybackDataReceived,
                true
            ),
            Messengers.playerState$.addReceiver(this.onPlayerStateChange),
            Messengers.timeUpdate$.addReceiver(this.onTimeUpdate),
            Messengers.playerError$.addReceiver(this.onPlayerError),
            Messengers.currentAd$.addReceiver(this.onAdChange),
            Messengers.appTeardown$.addReceiver(this.onAppTeardown),
        ];
    };

    removeEventListeners = () => {
        this.listeners.forEach((receiver) => receiver.unlisten());
        this.listeners = [];
    };

    muxEmit = (eventName, data) => {
        if (!window.mux) {
            console.warn("[acc] No mux analytics library on window");
            return;
        }
        window.mux.emit(this.muxPlayerId, eventName, data);
    };

    onPlaybackDataReceived = (playbackData) => {
        if (!window.mux || !playbackData) return;

        // start mux once
        this.playbackData = playbackData.playbackResponse;
        this.playbackSource = playbackData.matchingSource;

        // reset things
        this.hasPlayed = false;
        this.currentTimeMs = 0;

        const initTime = Date.now();
        const region = Store.RegionData.regionKey;

        const muxOptions = {
            debug: false,
            data: {
                env_key: this.envKey,
                player_init_time: initTime,
                viewer_user_id: Store.UserInfo.subject,
                sub_property_id: "7mle",
                player_name: "7mle Foxtel",
                player_version: `7mle Foxtel ${Store.appVersion}`,
                video_id: this.playbackData?.media.id,
                video_stream_type: "live",
                video_content_type: "linear",
                video_title: `${this.playbackData?.media.name}|${region}`,

                page_type: "watchpage",
                experiment_name: "Connected TV",
                video_language_code: "en",
                video_series:
                    this.playbackData?.media?.custom_fields.content_showname ||
                    "",
                video_cdn: this.playbackData?.media?.stream_type_cdn || "",
                video_duration: this.playbackData?.media.duration,
                video_stream_type: "live",
                video_content_type: "linear",
                viewer_device_name: `Foxtel-${Store.devicePlatform}`,
                viewer_device_category: "Foxtel",
                viewer_device_model: Store.deviceModel || "",
            },
            getPlayheadTime: () => {
                return this.currentTimeMs;
            },
            getStateData: () => {
                return {
                    player_is_paused: this.playerState !== PlayerStates.Playing, // Return whether the player is paused, stopped, or complete (i.e. in any state that is not actively trying to play back the video)
                    player_width: window.innerWidth, // Return the width, in pixels, of the player on screen
                    player_height: window.innerHeight, // Return the height, in pixels, of the player on screen
                    player_is_fullscreen: true, // Return true if the player is fullscreen
                    player_autoplay_on: true, // Return true if the player is autoplay
                    video_source_url: this.playbackSource.src, // Return the playback URL (i.e. URL to master manifest or MP4 file)
                };
            },
        };

        console.log(
            "[acc] MUX Client: init monitor",
            muxOptions.data.player_name
        );
        window.mux.init(this.muxPlayerId, muxOptions);
    };

    onPlayerStateChange = (playerState) => {
        const prevState = this.playerState;
        this.playerState = playerState;
        if (playerState === PlayerStates.Playing) {
            if (!this.hasPlayed) {
                this.muxEmit("playerready");
                if (prevState === PlayerStates.Seeking) {
                    // simulate seeked event since it is not defined
                    // but required by mux
                    this.muxEmit("seeked");
                }
                this.muxEmit("play");
                this.hasPlayed = true;
            }
            this.muxEmit("playing");
        } else if (playerState === PlayerStates.Seeking) {
            this.muxEmit("seeking");
        }
    };

    onTimeUpdate = (time) => {
        this.currentTimeMs = time * 1000;
        this.muxEmit("timeupdate", {
            player_playhead_time: this.currentTimeMs,
        });
    };

    onAdChange = (ad) => {
        const prevAd = this.currentAd;
        this.currentAd = ad;
        if (prevAd) {
            this.muxEmit("adended");
        }
        if (this.currentAd) {
            if (!prevAd) {
                this.muxEmit("adbreakstart");
            }
            this.muxEmit("adplay");
            this.muxEmit("adplaying");
        } else {
            if (prevAd) {
                this.muxEmit("adbreakend");
            }
        }
    };

    onPlayerError = (error) => {
        if (error) {
            this.muxEmit("error", {
                player_error_code: -1,
                player_error_message: error.message,
            });
        }
    };

    onAppTeardown = () => {
        if (this.hasPlayed) {
            this.muxEmit("destroy");
        }
        this.removeEventListeners();
        this.currentTimeMs = 0;
        this.hasPlayed = false;
        this.currentAd = null;
        this.playbackData = null;
        this.playbackSource = null;
        this.playerState = null;
    };
}

export default new MuxClient();
