import { Store } from "./helpers/seven-store";
import { MessengerLink } from "./helpers/MessengerLink";
import { Messengers } from "./helpers/Messengers";
import { NetworkConnectionType } from "./helpers/utils";
import { ErrorTypes, MLEError } from "../provider-utils";
import {
    getAndStoreConfig,
    getAndStoreRegion,
    getSevenPlaybackData,
} from "./seven-services/seven-api";
import {
    getAndStoreUserInfo,
    tryPerformTokenRefresh,
} from "./seven-services/seven-auth";
import NewRelicAnalytics from "./analytics/NewRelicAnalytics";
import SnowplowBridge from "./analytics/SnowplowBridge";
import MuxAnalytics from "./analytics/MuxAnalytics";

export default class SevenProvider {
    config;
    messengerLink;
    videoElement;

    constructor(config) {
        this.config = config;
    }

    /**
     * @typedef {import('../../app/get-asset').GetAssetRequest} GetAssetRequest
     * @typedef {import('../../app/get-asset').GetAssetResponse} GetAssetResponse
     */
    /**
     * @param {GetAssetRequest} args requested asset and device information
     * @returns {GetAssetResponse} streams for the requested asset
     */
    async getAsset({
        authToken,
        glassAssetLocator,
        glassContentOptions,
        deviceInfo,
    }) {
        const start = performance.now();
        let gracenoteId = getFileName(glassAssetLocator.pathname);
        const provider = glassAssetLocator.searchParams.get("provider");
        let providerId = glassAssetLocator.searchParams.get("providerid");

        const {
            glassConfigure: {
                brand: deviceBrand,
                osVersion: deviceOSVersion,
                model: deviceModel,
            },
            uuid: deviceId,
        } = deviceInfo;
        const { connectionType: networkConnectionType } = glassContentOptions;

        try {
            const sevenAuthData = JSON.parse(atob(authToken));
            const { refresh_token } = sevenAuthData;
            if (!refresh_token) throw new Error();
            Store.Auth.refresh_token = refresh_token;
        } catch (e) {
            throw new MLEError(
                "Could not parse Seven auth data",
                ErrorTypes.GENERIC
            );
        }

        console.info("[acc] SEVEN received startup data", {
            glassAssetLocator,
            authToken,
            glassAssetLocatorParsed: {
                provider,
                providerId,
            },
            glassContentOptions,
            deviceInfo,
        });

        let channelId;
        try {
            const channelInfo = JSON.parse(providerId);
            if (!channelInfo.channelId) {
                throw new Error();
            }
            channelId = channelInfo.channelId;
        } catch (err) {
            throw new MLEError(
                "Could not parse Seven channelID",
                ErrorTypes.CHANNEL_MAP
            );
        }

        console.info(
            `[acc] Accedo version ${Store.appVersion}, parent app version ${deviceInfo.appVersion}, player version ${deviceInfo.playerVersion}`
        );

        Store.deviceId = deviceId;
        Store.deviceBrand = deviceBrand;
        Store.devicePlatform = "SoIP"; // hardcode till firebolt is sunsetted
        Store.deviceModel = "Xi1"; // hardcode till firebolt is sunsetted
        Store.deviceFirmwareVersion = deviceOSVersion;
        Store.appIsRunning = true;
        if (networkConnectionType.toLowerCase() === "ethernet") {
            Store.deviceNetworkConnection = NetworkConnectionType.LAN;
        } else if (networkConnectionType.toLowerCase() === "wifi") {
            Store.deviceNetworkConnection = NetworkConnectionType.WIFI;
        }

        // Expect subpromises to throw their own MLEErrors
        await getAndStoreConfig();
        await Promise.all([getAndStoreRegion(), tryPerformTokenRefresh()]);
        await getAndStoreUserInfo();
        const sourceInfo = await getSevenPlaybackData(channelId);

        console.log(
            `[acc] passing back url ${sourceInfo.sourceUrl} after ${
                (performance.now() - start) / 1000
            }s`
        );
        return {
            aampInitConfigOverrides: {
                setLicenseCaching: false,
                ...this.config?.aampInitConfigRemoteOverrides,
            },
            streams: [
                {
                    url: sourceInfo.sourceUrl,
                    drmConfig: sourceInfo.drmLicenseUrl
                        ? {
                              [Store.preferredDrmSystem]:
                                  sourceInfo.drmLicenseUrl,
                              preferredKeysystem: Store.preferredDrmSystem,
                          }
                        : undefined,
                    requestHeaders: sourceInfo.requestHeaders,
                },
            ],
        };
    }

    monitoringSetup(videoElement) {
        console.log("[acc] call monitoringSetup");
        if (!this.messengerLink) {
            this.messengerLink = new MessengerLink(Messengers);
        }
        this.videoElement = videoElement;
        this.messengerLink.unlinkVideoElementListeners(this.videoElement);
        this.messengerLink.linkVideoElementListeners(this.videoElement);

        // expect modules to destroy themselves when appTeardown$ fires
        SnowplowBridge.init();
        MuxAnalytics.init();
        NewRelicAnalytics.init();
    }

    monitoringTeardown() {
        console.log("[acc] call monitoringTeardown");
        if (this.messengerLink && this.videoElement) {
            this.messengerLink.unlinkVideoElementListeners(this.videoElement);
        }
        Messengers.appTeardown$.send();
        Object.keys(Messengers).forEach((key) => Messengers[key].reset());
        Store.appIsRunning = false;
    }
}

function getFileName(url) {
    if (url) {
        return url.substring(url.lastIndexOf("/") + 1, url.lastIndexOf("."));
    }

    return "";
}
