import { NetworkConnectionType, generateUUID } from "./utils";
import { generateUUID } from "../helpers/utils";
import packageJson from "../package.json";

const adIdKey = "au.com.seven_MLE_AD_ID";
const sevenStagingConfigKey = "au.com.seven_USE_STAGING_CONFIG";

// run this in debugger to set staging config, then reload
// localStorage.setItem("au.com.seven_USE_STAGING_CONFIG", "true");

const useProdConfig = localStorage.getItem(sevenStagingConfigKey) !== "true";

let backupAdId = generateUUID();
let generateOztamSessionId = '';

export const Store = {
    appVersion: packageJson.version,
    deviceId: "",
    deviceModel: "",
    deviceBrand: "",
    devicePlatform: "",
    deviceMake: "foxtelglass",
    deviceFirmwareVersion: "",
    appIsRunning: true,
    preferredDrmSystem: "com.widevine.alpha",
    useProdConfig,
    Api: {
        configBase: useProdConfig
            ? "https://config.swm.digital"
            : "https://config-staging.inferno.digital",
        regSource: "7plus",
        platformId: "foxtel",
        platformVersion: "6.0.0",
        apiVersion: "4.8",
        apiKey: useProdConfig
            ? "CJSy6SIdYbDiD4YYnLLC1C7rcLWS4tlmtQlRDIoi9tUcf"
            : "Dh4H9pKXnCi07UzmbLnvWHbAG57jxqCMeogzNJbpSDp", // staging
    },
    deviceNetworkConnection: NetworkConnectionType.UNKNOWN,
    AppConfig: null,
    RegionData: {
        regionKey: "<empty>",
        marketKey: "<empty>",
        postcode: "<empty>",
        ipAddress: "<empty>",
        userMarket: "<empty>",
        isLocationVerified: false
    },
    UserInfo: {
        email: "<empty>",
        firstname: "<empty>",
        lastname: "<empty>",
        subject: "<empty>",
    },
    Auth: {
        access_token: "",
        expires_in: -1,
        refresh_token: "",
    },
    getOzTamId: () => {
       return generateOztamSessionId;
    },

    setOzTamId: (sessionId) => {
        if(!sessionId) {
            sessionId = generateUUID();
        }

        generateOztamSessionId = sessionId;
    },
    getAdId: () => {
        if (!localStorage) {
            return backupAdId;
        }
        let storedId = localStorage.getItem(adIdKey);
        if (storedId) {
            return storedId;
        }

        localStorage.setItem(adIdKey, backupAdId);
        return backupAdId;
    },
    // AFL feature flags
    isAflGeoEnabled: true,
    isUpdatedAuth2ApiEnabled: true,
    isLocationApiEnabled: true,
    isPlaybackLocationVerificationEnabled: true,
};
