import { Messengers } from "../helpers/Messengers";
import { Store } from "../helpers/seven-store";
import {
    ErrorTypes,
    MLEError,
    setTokenInDeviceStorage,
} from "../../provider-utils";
import { makeSevenAPIRequest } from "./seven-api";

export const tryPerformTokenRefresh = async (retry_count) => {
    const refreshToken = Store.Auth.refresh_token;

    var url = `${Store.AppConfig.endpoints.signIn.TOKEN}?platform-id=${Store.Api.platformId}&platform-version=${Store.Api.platformVersion}`;
    var postBody = `PlatformId=${Store.Api.platformId}&RegSource=${Store.Api.regSource}&refreshToken=${refreshToken}`;

    try {
        const newTokenRequest = await makeSevenAPIRequest(url, {
            method: "POST",
            body: postBody,
            headers: {
                "Content-Type": "application/x-www-form-urlencoded",
                "x-swm-apikey": Store.Api.apiKey, // include api key on auth2 endpoint requests only
            },
        });

        if (!newTokenRequest.ok) {
            throw newTokenRequest;
        }

        const newTokenResponse = await newTokenRequest.json();
        Store.Auth = newTokenResponse;
        setupRefreshTimer(newTokenResponse);
        // set in sky storage, no need to await or catch errors
        setTokenInDeviceStorage(
            "au.com.seven",
            btoa(
                JSON.stringify({
                    refresh_token: Store.Auth.refresh_token,
                    access_token: Store.Auth.access_token,
                    expires_in: newTokenResponse.expires_in,
                    token_type: newTokenResponse.token_type,
                })
            )
        );
    } catch (err) {
        console.warn("[acc] error refreshing seven auth", err);
        if (retry_count <= 2) {
            return await tryPerformTokenRefresh(retry_count + 1);
        } else {
            // geoRestricted flag set to false for sign in flow
            setTokenInDeviceStorage(
                "au.com.seven",
                btoa(JSON.stringify({
                    "geoRestricted": false
                })) 
            );
            throw new MLEError(
                "Could not perform 7Plus Auth refresh",
                ErrorTypes.AUTH,
                err.status
            );
        }
    }
};

let refreshTimeout;
const setupRefreshTimer = () => {
    refreshTimeout = setTimeout(async () => {
        console.log("[acc] 7Plus refreshing token...");
        await tryPerformTokenRefresh();
    }, Store.Auth.expires_in * 1000);
};

Messengers.appTeardown$.addReceiver(() => {
    if (refreshTimeout) clearInterval(refreshTimeout);
});

export const getAndStoreUserInfo = async () => {
    const userInfoBaseUrl = Store.AppConfig.endpoints?.signIn?.USERINFO;
    if (!userInfoBaseUrl) {
        throw new MLEError("No user info url in config", ErrorTypes.PROVIDER);
    }

    const userInfoUrl = `${userInfoBaseUrl}?platform-id=${Store.Api.platformId}&platform-version=${Store.Api.platformVersion}`;
    const postBody = `PlatformId=${Store.Api.platformId}&RegSource=${Store.Api.regSource}`;

    try {
        const userInfoRequest = await makeSevenAPIRequest(userInfoUrl, {
            headers: {
                "Content-Type": "application/x-www-form-urlencoded",
                "x-swm-apikey": Store.Api.apiKey, // include api key on auth2 endpoint requests only
                Authorization: "Bearer " + Store.Auth.access_token,
            },
            method: "POST",
            body: postBody,
        });
        if (!userInfoRequest.ok) {
            throw userInfoRequest;
        }

        const userInfo = await userInfoRequest.json();
        if (!userInfo.subject) {
            throw new MLEError(
                "No gigyaID found in user info response",
                ErrorTypes.PROVIDER
            );
        }
        Store.UserInfo = userInfo;
    } catch (err) {
        console.log(err);
        throw err;
    }
};
